<template>
    <div class="app-shell app-shell-bottom-navigation">
        <app-header
            :show="appHeader.show"
            :showMenu="appHeader.showMenu"
            :showBack="appHeader.showBack"
            :showLogo="appHeader.showLogo"
            :title="appHeader.title"
            :actions="appHeader.actions"
            :loading="isPageSwitching"
            @click-menu="handleClickHeaderMenu"
            @click-back="handleClickHeaderBack">
            <template slot="logo"></template>
        </app-header>
        <app-sidebar
            :show="appSidebar.show"
            :slideFrom="appSidebar.slideFrom"
            :title="appSidebar.title"
            :blocks="appSidebar.blocks"
            @close-sidebar = "handleCloseSidebar"
            @show-sidebar = "handleShowSidebar"
        >
            <template slot="logo1"><span></span></template>
        </app-sidebar>
        <div class="app-view-wrapper">
            <v-progress-circular
                indeterminate
                :size="50"
                v-show="isPageLoading"
                class="app-view-loading"/>
            <transition
                :name="pageTransitionName"
                @before-enter="handleBeforeEnter"
                @after-enter="handleAfterEnter">
                <router-view class="app-view"></router-view>
            </transition>
        </div>
        <app-bottom-navigator
            :show="appBottomNavigator.show"
            :navs="appBottomNavigator.navs"
            @click-nav="handleClickBottomNav"/>
    </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex';
import AppHeader from './components/appHeader';
import AppSidebar from './components/appSidebar';
import AppBottomNavigator from './components/appBottomNavigator';

export default {
    name: 'bottomNavigation',
    components: {
        AppHeader,
        AppSidebar,
        AppBottomNavigator
    },
    data () {
        return {};
    },
    computed: {
        ...mapGetters([
            'appHeader',
            'appSidebar',
            'appBottomNavigator',
            'isPageLoading',
            'isPageSwitching',
            'pageTransitionName'
        ])
    },
    methods: {
        ...mapActions([
            'setPageSwitching',
            'showSidebar',
            'hideSidebar',
            'activateBottomNav'
        ]),
        handleBeforeEnter() {
            this.setPageSwitching(true);
        },
        handleAfterEnter() {
            this.setPageSwitching(false);
        },
        handleClickHeaderBack() {
            this.$router.go(-1);
        },
        handleClickHeaderMenu() {
            this.showSidebar();
        },
        handleCloseSidebar() {
            this.hideSidebar();
        },
        handleShowSidebar() {
            this.showSidebar();
        },
        handleClickBottomNav({name}) {
            this.activateBottomNav(name);
        }
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    
    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    
        $material-custom := {
            bg-color: #fff
            fg-color: #000
            text-color: #000
            primary-text-percent: 0.87
            secondary-text-percent: 0.54
            disabledORhints-text-percent: 0.38
            divider-percent: 0.12
            active-icon-percent: 0.54
            inactive-icon-percent: 0.38
        }
        $material-theme := $material-custom
    


.app-shell
    position absolute
    top 0
    right 0
    bottom 0
    left 0
    width 100%
    height 100%
    display flex
    flex-direction column

    .app-view-wrapper
        flex 1
        position relative
        overflow hidden
        .app-view-loading
            position fixed
            top 50%
            left 50%
            transform translate(-50%, -50%)
            z-index 100
            color: $theme.primary
        .app-view
            position absolute
            top 0
            right 0
            bottom 0
            left 0
            overflow-x hidden
            overflow-y auto
            transition transform 0.4s cubic-bezier(.55, 0, .1, 1)
            background: $material-theme.bg-color
            color: $material-theme.text-color

            &.slide-left-enter
                transform translate(100%, 0)

            &.slide-right-enter
                transform translate(-100%, 0)

            &.slide-right-leave-active
                transform translate(100%, 0)

            &.slide-left-leave-active
                transform translate(-100%, 0)

</style>



